import React from 'react';
import styled from 'styled-components';

const Style = styled.div`
  @keyframes flicker {
    0% {
      opacity: 1;
    }
    3% {
      opacity: 0.4;
    }
    6% {
      opacity: 1;
    }
    7% {
      opacity: 0.4;
    }
    8% {
      opacity: 1;
    }
    9% {
      opacity: 0.4;
    }
    10% {
      opacity: 1;
    }
    100% {
      opacity: 1;
    }
  }

  svg {
    z-index: 100;
    position: relative;
  }

  svg #d {
    animation: flicker 6s infinite forwards;
  }

  svg #t {
    animation: flicker 10s infinite forwards;
  }

  svg #nk {
    animation: flicker 4s infinite forwards;
  }
`;

